import { useEffect, useState } from 'react';
import { Button, Box, TextField, Container, Typography, TableContainer, Paper, Table, TableHead, TableRow, TableCell, TableBody, TablePagination, LinearProgress } from '@mui/material';
import { Link } from 'react-router-dom';
import Page from 'src/components/Page';
import { useDispatch, useSelector } from 'src/store';
import { loadBlocks } from 'src/store/slices/slate/block';

import FormLabel from 'src/components/FormLabel';

const BlockExplorer = () => {
  const [searchString, setSearchString] = useState();
  const dispatch = useDispatch();

  const { blocks, blocksCount, blocksPerPage, blocksPage, loading } = useSelector((state) => state.slate.block);

  useEffect(() => {
    dispatch(loadBlocks({page: 0}));
  }, [dispatch]);

  const handleChangePage = (event, page) => {
    dispatch(loadBlocks({page: page}));
  };

  const handleSearch = (str) => {
    dispatch(loadBlocks({page: 0, hash: str}))
  }
  
  return (
    <Page title="Blocks - Block Explorer">
      <Container maxWidth="lg">
        <Box display="flex" alignItems="center">
          <Typography style={{flexGrow:1}} gutterBottom variant="h5" component="div" sx={{ mt: 2 }}>
            Latest blocks
          </Typography>
          <Box display="flex" alignItems="center">
            <FormLabel marginBottom={0} label="Search" />
            <TextField
              fullWidth
              size="small"
              margin="dense"
              name="search"
              style={{margin: '0 12px'}}
              onChange={(e)=> setSearchString(e.target.value)}
            />
            <Button variant="contained" onClick={() => handleSearch(searchString)}>Search</Button>
          </Box>
        </Box>
        <TableContainer component={Paper} sx={{ my: 2 }}>
          {loading && <LinearProgress />}
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Hash</TableCell>
                <TableCell align="right">Height</TableCell>
                <TableCell align="right">Transaction Count</TableCell>
                <TableCell align="right">Size</TableCell>
                <TableCell align="right"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {blocks.map((b) => (
                <TableRow key={b.hash} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                  <TableCell component="th" scope="row">
                    {b.hash}
                  </TableCell>
                  <TableCell align="right">{b.height}</TableCell>
                  <TableCell align="right">{b.transaction_counts}</TableCell>
                  <TableCell align="right">{b.size}</TableCell>
                  <TableCell align="right">
                    <Link to={`/block-explorer/blocks/${b.hash}`}>View</Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          <TablePagination rowsPerPageOptions={[]} component="div" count={blocksCount} rowsPerPage={blocksPerPage} page={blocksPage} onPageChange={handleChangePage} />
        </TableContainer>
      </Container>
    </Page>
  );
};

export default BlockExplorer;
